body {
  color: #333;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.container {
  margin-top: 50px;
}

.form-control {
  color: #333;
  background-color: #e5e5e5;
  border: none;
  border-radius: 0;
  margin-bottom: 20px;
  font-size: 16px;
}

.form-control:focus {
  box-shadow: none;
  color: #333;
  background-color: #fff;
  border: none;
}

.form-check-label {
  color: #4c4c4c;
  margin-left: 5px;
  font-size: 14px;
}

.btn-primary {
  text-transform: uppercase;
  background-color: #333;
  border: none;
  border-radius: 0;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
}

.btn-primary:hover {
  background-color: #4c4c4c;
}

.forgot-password {
  color: #4c4c4c;
  text-align: center;
  margin-top: 20px;
  font-size: 14px;
}

.forgot-password a {
  color: #8c8c8c;
  text-decoration: none;
}

.forgot-password a:hover {
  color: #333;
}

#toast-container {
  margin-top: 20px;
}

.toast-header {
  justify-content: space-between;
  display: flex;
}

button.close {
  border: none;
  outline: none;
}

